<template>
	<view class="goods_detail">
	<swiper indicator-dots>
		<swiper-item v-for="(item,index) in swipers" :key="index">
			<image :src="item.content"></image>
		</swiper-item>
	</swiper>
	<view class="box1">
		<view class="price">
			<text>￥{{ info.x_price }}</text>
			<text>￥{{ info.c_x_price }}</text>
		</view>
		<view class="goods_name">
			{{ info.goods_name }}
		</view>
	</view>
	<view class="line"></view>
	<view class="box2">
		<view>货号：SD12333333</view>
		<view>库存：233</view>
	</view>
	<view class="line"></view>
	<view class="box3">
		<view class="tit">详情介绍</view>
		<view class="content"> <rich-text :nodes="info.content"></rich-text></view>
	</view>
	
	<view class="goods_nav">
		<uni-goods-nav :fill="true"  :options="options" :buttonGroup="buttonGroup"  @click="onClick" @buttonClick="buttonClick" />
	</view>
	</view>
</template>

<script>
	import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
	export default {
		data() {
			return {
				id:0,
				swipers:[],
				info:[],
				options: [{
				     icon: 'headphones',
				     text: '客服'
				 }, {
				     icon: 'shop',
				     text: '店铺'
				 }, {
				     icon: 'cart',
				     text: '购物车',
				     info: 2
				 }],
				 buttonGroup: [{
				   text: '加入购物车',
				   backgroundColor: '#ff0000',
				   color: '#fff'
				 },
				 {
				   text: '立即购买',
				   backgroundColor: '#ffa200',
				   color: '#fff'
				 }
				 ]
			}
		},
		onLoad(options){
			this.id = options.id;
			this.getSwipers();
			this.getDerailInfo();
		},
		components: {uniGoodsNav},
		methods: {
			async getSwipers(){
			const res = await this.$myRequest({
						// url:"/carousel?id="+this.id //正确写法
					url:"/carousel"
				})
				this.swipers = res.data.obj.data.msg;
			},
			
		async getDerailInfo(){
			const res = await this.$myRequest({
						// url:"/goodsList?id="+this.id //正确写法
					url:"/goodsList"
				})
				this.info = res.data.obj.data.msg[0];
				console.log(res)
		},
		 onClick (e) {
		        // uni.showToast({
		        //   title: `点击${e.content.text}`,
		        //   icon: 'none'
		        // })
				console.log(e)
		      },
		      buttonClick (e) {
		        console.log(e)
		        this.options[2].info++
		      }
			
		}
	}
</script>

<style lang="scss">
	.goods_detail{
		swiper{
			height: 700rpx;
			image{
				width: 100%;
				height: 100%;
			}
		}
		.box1{
			padding: 10px;
			.price{
				font-size: 35rpx;
				color: $shop-color;
				line-height: 80rpx;
				text:nth-child(2){
					color: #ccc;
					font-size: 28rpx;
					text-decoration: line-through;
					margin-left: 20rpx;
				}
			}
			.goods_name{
				font-size: 32rpx;
				line-height: 60rpx;
			}
		}
		.line{
			height: 10rpx;
			width: 750rpx;
			background-color: #eee;
		}
		.box2{
			padding: 0 10px;
			font-size: 32rpx;
			line-height: 70rpx;
			
		}
		.box3{
			padding-bottom: 50px;
			.tit{
				font-size: 32rpx;
				padding-left: 10px;
				border-bottom: 1px solid #eee;
				line-height: 70rpx;
			}
			.content{
				padding: 10px;
				font-size: 28rpx;
				color: #333;
				line-height: 50rpx;
			}
		}
		.goods_nav{
			position: fixed;
			bottom: 0;
			width: 100%;
		}
	}
</style>
